/* eslint-disable */
// CSS
import "../sass/lbw.sass";
// JS
require('aframe-gltf-part-component');
require('aframe-orbit-controls');
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
import { OutputPass } from "three/examples/jsm/postprocessing/OutputPass";
import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass";
import { GTAOPass } from "three/examples/jsm/postprocessing/GTAOPass";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { NoBlending } from "three";
import { gsap } from "gsap";
//網頁資料下載完成
document.addEventListener('DOMContentLoaded', function() {
    const sceneEl = document.querySelector('#scene');
    sceneEl.setAttribute('load-complete', '');
});

//3D資源下載完成
AFRAME.registerComponent('load-complete', {
    init: function() {
        // SCENE
        const sceneEl = document.querySelector('#scene');
        const cameraEl = document.querySelector('#basic_camera');
        
        // cameraEl.getObject3D('camera')

        // 创建一个时间线
        // const timeline = gsap.timeline();

        // 添加 FOV 动画到时间线
        gsap.to(cameraEl.getObject3D('camera'), {
            delay: 3,
            fov: 60,
            ease: "power1.inOut",
            duration: 6,
            onUpdate: () => {
                cameraEl.getObject3D('camera').updateProjectionMatrix();
                cameraEl.setAttribute('orbit-controls', {
                    target: '0 0 0',
                    minDistance: 240 / cameraEl.getObject3D('camera').fov,
                    maxDistance: 300 / cameraEl.getObject3D('camera').fov,
                    initialPosition: '0 0 ' + 240 / cameraEl.getObject3D('camera').fov,
                    rotateSpeed: 2,
                    zoomSpeed: 2,
                    enablePan: false,
                    maxPolarAngle: 90
                });
            },
            onComplete: () => {
              // 动画播放完毕后，启用旋转功能
              cameraEl.setAttribute('orbit-controls', { enableRotate: true });
            }
        });

        // 启动时间线动画
        // timeline.play();

        window.addEventListener( 'resize', onWindowResize );
        function onWindowResize() {
            let _width = window.innerWidth;
			let _height = window.innerHeight;
            sceneEl.renderer.setSize( parseInt(_width), parseInt(_height) );
            sceneEl.camera.aspect = parseInt(_width)/parseInt(_height);
            sceneEl.camera.updateProjectionMatrix();
		}
        onWindowResize();
    }
});